<template>
  <div class="Echarts">
    <div id="left01"></div>
  </div>
</template>

<script>
export default {
  name: 'LeftRequest',
  methods: {
    leftChart() {
      // 基于准备好的dom，初始化echarts实例
      const leftChart = this.$echarts.init(document.getElementById('left01'));

      // 指定图表的配置项和数据
      const option = {
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
        },
        yAxis: {
          type: 'value',
        },
        series: [{
          data: [150, 230, 224, 218, 135, 147, 260],
          type: 'line',
        }],
      };

      // 使用刚指定的配置项和数据显示图表。
      leftChart.setOption(option);
    },
  },
  mounted() {
    this.leftChart();
  },
};
</script>

<style scoped>
  .Echarts{
    width: 100%;
  }
  #left01{
    width: 100%;
    height: 480px;
  }
</style>
