<template>
  <div class="Echarts">
    <div id="middle01"></div>
  </div>
</template>

<script>
export default {
  name: 'Trequest',
  methods: {
    requestChart() {
      // 基于准备好的dom，初始化echarts实例
      const requestChart = this.$echarts.init(document.getElementById('middle01'));

      // 指定图表的配置项和数据
      const t10option = {
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
        },
        yAxis: {
          type: 'value',
        },
        series: [{
          data: [120, 200, 150, 80, 70, 110, 130],
          type: 'bar',
          showBackground: true,
          backgroundStyle: {
            color: 'rgba(180, 180, 180, 0.2)',
          },
        }],
      };

      // 使用刚指定的配置项和数据显示图表。
      requestChart.setOption(t10option);
    },
  },
  mounted() {
    this.requestChart();
  },
};
</script>

<style scoped>
  .Echarts{
    width: 100%;
  }
  #middle01{
    width: 100%;
    height: 480px;
  }
</style>
